        ],
        scenic:[
          {id:"1",image:"./static/image/recommendScenic1.jpeg",name:"同州湖风景区",grade:"国家AAAA级旅游景区",info:"大荔县同州湖风景区位于陕西渭南大荔城区段，占地面积4平方公里，国家AAAA级旅游景点，已累计投资3亿元，完成6.3公里两岸堤防、1.75公里中隔段、360亩生态景观工程、1000亩湖面蓄水工程、5公里环湖自行车绿道、100辆自行车站点、1000部生态停车场等配套工程及音乐喷泉、福寿桥、康宁桥等20多处景点建设，可容纳5-10万人游乐观赏，将于六月全面向社会开放，填补了大荔县历史上无公园的空白，成为集文化、民俗、休闲、体验、旅游为一体的新景区。\n" +
              "　　湖为核心，建设南至官池镇驻地、北至冯翊路、西至商贸大道的同州湖4A级景区，促进产城、景城融合发展。4月10日，同州湖景区主要组成部分之一的同州湖西路开工建设。\n" +
              "\n" +
