              "\n" +
              "　　设施\n" +
              "\n" +
              "　　同洲湖景区设施齐全，1000亩湖面蓄水工程以及环湖自行车绿道、环湖电瓶车道、六角亭、橡胶坝、亲水平台、停车场等配套工程，中国红标志性景区大门、环湖步道、福寿康宁桥、儿童乐园、综合性健身区、中心广场、音乐喷泉、文化栈桥、管理设施、荷花池、码头、雕塑工艺品、生态卫生间、环湖灯带、中隔堤、北大堤石台阶、弱电、音乐草坪灯等其它配套工程正在加紧实施，预计将于“五一”前向社会开放，成为集文化、民俗、旅游为一体的县城南户门、大荔新景点。"},
          {id:"2",image:"./static/image/recommendScenic2.jpg",name:"平罗农业公园 ",grade:"国家AAA级旅游景区",info:"平罗农业公园地处县城以东8公里处，占地3000亩，总投资1.2亿元，建设内容包括尊天现代农业园区、中心村和生态旅游区三大部分。一是调整产业结构，立足本地资源优势，以增收、增效为核心，引进了陕西大荔尊天农业有限公司发展现代农业，建设尊天现代农业园区，带动群众增收致富。园区流转土地2400亩，建成设施大棚800亩、日光温室100座，栽植了温室冬枣、小乳瓜、圣女果、水培菜等各种绿色无公害蔬菜瓜果，已成为国家级设施蔬菜生产标准园和省级现代农业园区。二是完善基础设施，中心村完成了村内33条道路硬化，安装太阳能路灯90盏；建成村文化广场，配套健身器材11套；绿化栽植各类苗木1.2万株；建成了标准化公立幼儿园，进一步提高了农民生活质量、改善农村生态环境。三是发展乡村旅游，生态旅游区建成了民俗村停车场、小广场及其配套项目建设，栽植了温室花卉和50 亩露天花海、120亩观光林，建成了“尊天马术俱乐部”“秋千园”“湿地观光、垂钓、畅游芦苇荡”等，已成为乡村旅游的一大热点。平罗农业公园全面建成后，将具备“吃、住、行、游、购、娱”六大功能，成为集民俗体验、户外娱乐、湿地疗养、休闲度假、农业观光、会议会展等于一体的乡村旅游度假胜地。"},
          {id:"3",image:"./static/image/recommendScenic3.jpeg",name:"福佑古寨景区 ",grade:"国家AAA级旅游景区",info:"福佑古寨居黄河西岸，宛如一颗璀璨的绿宝石镶嵌在朝坂塬头。登台放眼，远眺中条山烟云缭绕，俯观渔湖镜天，十万亩水面波光粼粼，“百福图”大地景观尽收眼底；望大西高铁黄河大桥宛如一道镶着银边的虹，堪与东岸鹳雀楼相媲美；移转目光，便可见博大浩瀚的母亲河黄河。置身其境心旷神怡，好一派黄河高原上的江南水乡！"},
          {id:"4",image:"./static/image/recommendScenic4.jpg",name:"八鱼石墓博物馆 ",grade:"国家AAA级旅游景区、文化旅游景点",info:"大荔县八鱼石墓博物馆依石墓群原址而建，紧靠原址北边，坐北朝南，新建成的石墓博物馆大气恢弘，四面高墙筑垒，更显神秘。\n" +
              "　　大荔八鱼李氏家族石墓群，共发现大型石墓22座，清理发掘11座，从已发掘的情况来看，墓葬结构、规模及出土实物，对研究清代社会的政治、经济、官僚制度，乃至当时社会葬俗文化，特别是其书法绘画和石雕石刻等都具有重要价值。\n" +
              "\n" +
              "　　石墓群位于黄河流域华山脚下的渭洛河附近的沙苑边缘中段，距大荔县城仅12公里，这里一马平川，土地肥沃，地下物藏丰富。早在秦汉时期，这里就是皇家放马养鹿之地，史称\"沙苑\"。\n" +
              "\n" +
              "　　明清两代，同州辖治，商贸繁荣，文化生活灿烂多姿，据出土墓志记载，此清代李氏望族墓地群，最早可上朔到乾隆时期，最晚延续到民国末年，延续300余年。每座石墓和墓石构件上都雕刻书法绘画装饰图案，达400幅之多。雕刻手法应有尽有，圆雕、高浮雕、浅浮雕、减地刻、阴刻等巧妙运用构成一个和谐的统一体．充分表现出雕刻者精湛和娴熟的技艺，也说明了墓室主人李氏在当地的社会地位和文化修养。石雕、石刻中有山水、人物、动物、花草等图案及文字楹联。\n" +
              "\n" +
              "　　1号墓墓主李怀瑾，该墓共有书画雕刻41幅，其中两石柱上刻楹联；'后嗣繁昌漫言钟秀，吾亲寿考是谓治谋'。石柱上楣额刻三幅图案，左右两幅被破坏，中间图案是一老者在健身，左右各有一对老年夫妇对座观看，身旁男仆、侍女、儿童相伴。院落石柱侧壁上刻'五蝠（福）捧寿'图案。庭堂南壁为，隶书石刻'得梅之青，与松俱古'的条幅，两侧有幅对联，'文华仙掌露、人品玉壶冰'。北壁雕刻内容为青竹四条屏画。东、西两壁刻有团扇画、方框画、杂宝图。中墓室石壁刻太极八封图。\n" +
              "\n" +
              "　　2号墓墓主为李树德（1804一1877）字滋亭，皇帝浩授中宪大夫，道元职衔，禀贡生出身。曾做兴平县儒学教谕。其院落对联为'一邱方是安闲地、万古常同兜率天'。楣额上刻三格，左格为兰花，右格为竹子，中间是杂宝图。庭堂南壁刻'忠'字．北壁刻'孝'字。字体均为楷书．一米见方。刚劲有力，体正大方。给人以德性为本之教。中墓室前有一对圆雕童男童女。\n" +
              "\n" +
              "　　3号墓墓主李天培（1830-1875），字介候，诰封资政大夫赏戴花翎候选郎中。一生喜好结交品行端正的名人雅士，经常救助贫苦乡邻。其墓地对联为'翠柏苍松一带浓阴庇寿城，青山绿水四围秀气绕佳城'。南壁屏风雕刻松鹤图、北壁为双鹿圈。院落和庭堂上均为花鸟、人物、山水楼阁画。中墓室前有幅对联'地接蓬莱学可传经筑石室、名登仙籍才培作赋咏罗天'。后壁刻有一屏风。南墓室前对联为'泉壤百年皆聚瑞、闺门千载亦流芳'、横批'泉台挹秀'，北墓室对联为'宝婺星辉垂额范，瑶池仙境慰的魂'。横批'漆灯朗照'。\n" +
              "\n" +
              "　　4号墓墓主李怀珍原配夫人董夫人。墓葬院落对联为'董草堂前风月冷，芙蓉城里梦魂香'。院落南壁有一屏风，北壁被破坏，院落、庭堂刻满花鸟、人物、果实、树木、山水等图。此墓只有两个墓室（一般为三室），南室对联为'别有洞天培小隐，新成庐舍乐长眠'。北室为'郁郁泉台埋白玉，深深庭院隔红尘'。两墓室后壁雕有人物故事画。\n" +
              "\n" +
              "　　5号墓墓主李怀珍（1807-1862），字聘卿，皇帝浩授中宪大夫，钦加道衔，赏戴花翎，议叙郎中候补充禄寺暑回，是当时级别、职位较高的京官。曾祖父李壁，祖父李朝恩，父亲李忠清都是朝廷命官。其院落、庭堂、南墓室均被破坏，只剩中、北墓室。中墓室后壁浅浮雕海水波涛、波涛上有仙鹤、楼台等。北墓室后壁刻有海水、海水中有一瓶，瓶中插三戟。\n" +
              "\n"},
          {id:"5",image:"./static/image/recommendScenic5.jpg",name:"丰图义仓",grade:"国家AAA级旅游景区、全国重点文物保护单位",info:"丰图义仓位于在陕西省大荔县城正东17公里处朝邑粮站大院内。\n" +
              "　　丰图义仓是建于清光绪八年（1882）的一座民办仓库，是我国目前所存无几的清代大型粮仓之一。该仓由晚清朝邑赵渡人，清朝著名理财家“救时宰相”阉敬铭倡议修建，历时四年建成，于光绪九年（1886）正式投入使用，开始存粮。当时被慈禧太后朱批为“天下第一仓”。解放后一直由大荔县朝邑粮站使用。\n" +
              "\n" +
              "　　丰图义仓的主体建筑是一座以砖结构为主的窑群式仓城，外观酷似一座古城。仓库建于仓城墙体之内，环内城一周排列窑洞58面，每窑可储粮90吨。共可储粮5200吨。仓城外又围有土筑寨墙，墙外有城壕。仓寨占地4.5万平方米。北仓城上中部建有仓楼一座，为紫阳仓祖朱文公祠。城内所有建筑均为清光绪年间所建。\n" +
              "\n" +
              "　　丰图义仓是一座以储粮为主要功能的建筑，又具有坚固的安全防御系统，从某种意义上可以说是具有防御性质的军事建筑。在利用地形和仓城建筑设计方面，确有许多独到之处：将仓置于城墙墙体之中是仓库建筑上的创新，既利用了城墙墙体的厚实，便于恒定粮温，又节约了建筑材料。丰图义仓的高厚寨墙在保证储存粮食安全的同时，又理所当然的成为地处该仓东面崖下朝邑城的卫城。因为其北与东岳庙相对，共同扼守南、北两个寨子之间的历史著名驿道----“朝坂古道”。体现了储粮和安全防御的双重功能。\n" +
              "\n" +
              "　　丰图义仓的完好保存和继续储粮，对于研究我国古代仓政、仓储历史、仓储文化、粮仓建筑艺术提供了翔实的历史资料，同时对研究封建社会后期关中东部的经济发展、军事攻防提供了重要的参考资料。\n" +
              "\n" +
              "　　2006年丰图义仓作为清代的古建筑，被国务院列入第六批全国重点文物保护单位。"},
          {id:"6",image:"./static/image/recommendScenic6.jpg",name:"大荔南湖“小三亚”",grade:"国家AAA级旅游景区",info:"地处沙苑经开区的南湖项目是渭南市2017年重点建设项目。景观节点工程主要有：建设东西长约1200m、南北宽约210m、库容量为118万m3的水库主体、入水建筑物及坝顶道路、绿化工程；160m南北横跨沙苑湖的八亭桥、湖心岛瀑布等景观节点；建设10万㎡景观绿化、3000m的人行步道、儿童戏沙区及游泳区、体育休闲等设施，随着沙苑南湖的开发和不断完善，这里将成为群众休闲娱乐、修身养性的好地方，让广大市民在美丽大荔创建中感受到越来越多的幸福感，无不为大荔的美景风光而感到自豪。"},
          {id:"7",image:"./static/image/recommendScenic7.jpg",name:"大荔沙苑 ",grade:"旅游景区",info:"夏日里，美丽的沙苑呈现出“风吹草地见牛羊”的迷人景象。再过五年，这里将变成国家5A级沙漠生态休闲度假旅游景区，目前，大荔县已按照五年规划在紧锣密鼓地实施。\n" +
              "　　沙苑地处渭洛河夹槽地带，东西八十里，南北三十里。西周秦汉时期灌草植被丰富，动物种类繁多，为历朝的牧马场所。面积400多平方公里，自古以来林湖相依、草木茂盛、人文荟萃、神秘神奇，是唐文化的重要承载地，是我国内陆平原最大的沙漠沙丘地带。据《同州府志》里说：“沙苑在县南洛渭之间，亦名沙海，沙泽其中盆起者曰沙阜。东跨朝邑，西至渭南，南连华州。广八十里，其沙随风流徙，不可耕植。”凡提及大荔县时，都知道沙苑，它包括现在的苏村镇、羌白镇、韦林镇、官池镇等地区。\n" +
              "\n" +
              "　　由于沙苑面积广，变化大，到处凸凹不平，遍地水、碱、草、沙，因而，群众对这种复杂的地形变化，又各有其不同的称谓：高的地方叫沙阜，低的地方叫沙洼，有水的地方叫沙地，有碱的地方叫沙卤，有草的地方叫沙滩，平坦的地方叫沙板，黄沙一片叫沙漠，沙砾大的叫沙碛。据文献记载，1956年考古中发现\"沙苑文化\"是中石器时代的典型代表，它佐证了大荔沙苑是黄河流域原始农业和原始畜牧业萌芽之地，沙苑地区汉代养畜植树，唐为皇家园林，明时变牧为农，明末山西、河南迁来大量移民，沙苑进入大面积农桑时代。\n" +
              "\n" +
              "　　近年来，大荔县紧锣密鼓，依托沙苑自然风光、人文历史、民俗风情等旅游资源和区位优势，力争用五年时间，逐步建成西安国际化大都市沙漠生态休闲度假景点，美丽的沙苑将成为四面八方游客观光游玩的好去处。"},
          {id:"8",image:"./static/image/recommendScenic8.jpg",name:"三河湿地自然保护区 ",grade:"旅游景区",info:"三河湿地自然保护区地处渭南市关中平原的东端，是黄河、渭河、洛河的交汇区。北起大荔县华原乡，南到老西临公路，东以黄河为界，与山西、河南相临，西沿渭河至华阴、潼关三县。南北长约40公里，东西约宽15公里，总面积4.67万公顷。\n" +
              "　　三河湿地境内地势平坦宽阔，河漫滩地和一级阶地被渭河和洛河切割为三大块。沿黄河呈南北带状，沿渭河又呈东西带状。应受黄河泥沙影响，处滩高于内滩，常年积水，形成沼泽、草甸和盐碱地带。保护区有六条支流注入黄河和渭河，到了汛期，黄河水常倒流入渭河和洛河，成为这一地区湿地的主要成因。\n" +
              "\n" +
              "　　保护区属暖温带大陆性半干燥、半湿润季风气候，海拔在330米-335米之间，地势较周围偏低，受秦岭和中条山所形成的潼关风口的影响，气候具有明显的地域特征，春季干燥升温快，夏季炎热日照长，秋季降温多阴雨，冬季寒冷少雪雨。\n" +
              "\n" +
              "　　独特的地理位置和典型的自然地理环境为湿地动植物资源和生物多样性提供了繁衍生息的场所。这里的森林植被为暖温带落叶、阔叶林。植被群落有芦苇群落、盐蓬群落、杯柳群落、草甸群落等。丰富的植物资源和植物群落的多样性，以及大量水生小动物的存在，为水禽提供了栖息、觅食和繁殖的良好环境，形成了三河湿地独特的生态环境。\n" +
              "\n" +
              "　　据初步调查，区内有脊椎动物27目53科110属140中，虾、螺、蚌等家壳类动物20余种，蛇、蛙等爬行动物以藻类、小昆虫及微生物等为食料，其本身又成为野生水禽的食物来源，形成湿地特有的生物链。"}
        ],
        food:[
          {id:"1",image:"./static/image/recommendFood1.jpg",name:"水盆羊肉",grade:"",info:"水盆羊肉起源于明朝崇祯年间，因在农历六月上市，又称\"六月鲜\"。大荔的水盆羊肉，久负盛名。以剔骨鲜羊肉、骨头、桂皮、花菽、小茴香、草果、精盐、味精为原料，分原料处理、煮羊肉、调制熟羊肉三道工序，食用时用烧饼或白吉馍同吃，佐以糖蒜、辣子酱、鲜蒜瓣，则肉烂汤清，肥而不腻，清醇可口，别具风味。\n" +
              "　　　　昔日的同州府衙，是水盆羊肉的故乡，最早烹调羊肉，用的是砂锅，所烹调的羊肉，瘦肉红润，肥肉白亮，羊汤清澈见底，讲究暗油醇香，食之浓香可口。由于砂锅形状如盆，故称\"水盆羊肉\"。随着时间的推移和食客数量的增加，以及食用餐具的逐渐发展下，用碗或盘盛肉食用的，人们仍习惯称为\"水盆羊肉\"。\n" +
              "\n" +
              "　　　　加工\"水盆羊肉\"，首先要求羊肉在煮以前必须反复清洗，使羊肉雪白发亮，无血丝，再用羊骨汤熬制数小时后，才配以各种混合调料，经过五六个小时的焖制而成。\n" +
              "\n" +
              "　　　众所周知，在大荔吃\"水盆羊肉\"，还离不开大荔的\"月牙烧饼\"。而此烧饼，是由于人们食用水盆羊肉的需要而渐次形成的。过去吃羊肉时，以烧饼为主食，而早先的烧饼是圆的，要夹肉时，还需把烧饼掰成两半，既参差不齐，且很不方便。在生活实践中，人们逐渐意识到，与其把烧饼掰成两半，不如在烤制前直接切成两半，因这种既方便也美观烧饼状如月牙，遂称\"月牙烧饼\"。\n" +
              "\n" +
              "　　　　月牙烧饼，热吃脆，冷吃酥。多年来还流传着这样一个顺口溜，\"南京到北京，同州府里好烧饼，掂起轻，摸着厚，空空里面能夹肉\"。所以，人们在食用水盆羊肉时，老道食客习惯把碗里的肉往\"月牙烧饼\"里一夹，两手轻轻地一捏，美美地喝一口醇香的羊肉汤，咬一口酥脆的月牙烧饼，啊！那才是一种回味无穷的美味享受。正所谓\"大荔水盆羊肉香，月牙烧饼拌清汤。同盛合美君自来，滋补养胃体健康\"！\n" +
              "\n" +
              "　　　　\"同盛合\"水盆羊肉店，眼下还推出了手抓羊排、特色羊头、烧羊腿、冷冻羊腿、凉拌羊杂、烩羊杂、酱牛肉等，又配备烹制调料，让身在外地而喜欢吃\"水盆羊肉\"的老乡可以方便食用。因此，\"同盛合\"为全市的\"水盆羊肉\"掀起了一次全新革命。\n" +
              "\n"},
          {id:"2",image:"./static/image/recommendFood2.jpg",name:"蜜汁轱辘",grade:"",info:"蜜汁轱辘源于何时，无从考究。这是一道久负盛名的同州名菜，因其状为球体，当地把这种形体的东西俗称轱辘，所以也把种用大油、淀粉适当配置，用煎油炸过，又勾以蜜称汁（今多用白糖）色泽金黄，外脆里嫩，香甜油润的这一道菜叫蜜汁轱辘。\n" +
              "　　【蜜汁轱辘】绝对是正宗的陕西菜，石头轱辘自然是不能吃的，实际上在大荔当地的方言中，圆圆的东西就称之为“轱辘”。蜜汁轱辘其形圆如轱辘，色泽金黄，咬一口外脆里酥，又香又甜。"},
          {id:"3",image:"./static/image/recommendFood3.jpg",name:"八宝肉酱辣子",grade:"",info:"八宝辣子又叫什锦辣子、酱辣子，是陕西省渭南地区的特色小吃，也是当地宴席上必不可少的一道菜。相传是祖籍朝邑(今属大荔县)的清朝阁老阎丹初从宫廷中带回后传入市肆的。它以多种原料佐以地方特产秦椒煸炒而成，故称“八宝肉辣子”。八宝辣子口感脆爽，香辣开胃，用于佐餐夹馍，堪称“饭扫光”、“馍遭殃”。\n" +
              "　　　　八宝辣子是大荔最传统的一道菜。说是八宝，无非是对众多原料的概称，不一定非要八样原料，可多可少。但有三样是必不可少的，一是辣椒面，二是咸菜丁，三是大肉丁。一辣二咸三香是八宝辣子的根本。当然，既然是八宝辣子，就不会这么单调，一般会就地取材，或因季节的不同，再加入别的原料，如黄豆、红萝卜丁、莲菜丁、蒜薹，条件好点的，还会有玉兰片甚至竹笋等等，总之用到的菜蔬都是比较脆生的。这些东西放进去，真要数起来够八样，颜色也是红、绿、白、黄、黑，煞是热闹好看，口感也会“丰富多彩”。\n" +
              "\n" +
              "　　　　炒制八宝辣子是一个看起来简单的活，但要做好、做地道还真有许多讲究。其一是选料，辣椒面要上乘的秦椒加工而成，最好是农家自己拣选、晾晒、炕烤并手工碾制的，这样的辣椒面非常干净纯粹、不会掺假，因为炕制过程放了少许油，所以辣椒面本身就辣中带香。另外，要选肥瘦相间的五花肉，太瘦则菜品柴，太肥则腻。至于咸菜，则必选俗称芥疙瘩的大头菜。这三样齐备，其它的菜蔬如红萝卜、莲菜、蒜薹、黄豆等等可根据主观需要和客观情况随性而定，总之要脆生耐嚼。其二是炒制，油要适度，不可太多太少。先肉后菜再撒辣椒面，急火快炒，掌握好火候，尤其是放了大量的辣椒面，稍微过火便会焦苦。当然，炒辣椒嘛，肯定会呛得厨师很辛苦。\n" +
              "\n" +
              "　　　　原料：猪后腿肉、葱伴侣酱、葱、豆腐(或豆腐干都可以)、花生豆(花生豆提前用凉水浸泡一个晚上)、五香豆豉、白菜帮、红萝卜。\n" +
              "\n" +
              "　　　　做法：1、将猪后腿的肥瘦肉分开，肥瘦肉各半。分别切成8mm左右的方丁。其他菜也都均切成和肉丁大小相似的方丁，红萝卜可稍微在小点5mm即可。\n" +
              "\n" +
              "　　　　2、将肥肉丁放入炒锅，置火上，火要是最小火。用铲子不停的翻动直至将里面的大油炸出来。待肥肉炸成金黄色放入辣椒面(可多放点辣椒因为炸后辣椒不辣，如果炸出来的大油少可适量加些菜油)。此时迅速放入瘦肉丁翻炒至变色，再放入葱姜稍炒并加入盐和调料粉。\n" +
              "\n" +
              "　　　　3、接下来最重要的一步就是炒酱，1斤肉大概要100克酱。酱炒出香味后先后放入，豆腐、红萝卜、白菜丁翻炒至八成熟。最后放入花生米和五香豆豉炒熟出锅。\n" +
              "\n" +
              "　　　　八宝辣子称为“八宝”并不是一定要有八样食材，而且形容食材种类多，但是必不可少的有肉丁、咸菜、辣椒面，肉丁要有肥有瘦，咸菜选用大头菜，辣椒要选用优质秦椒。除此三样外，可根据实际情况选择时令蔬菜，以口感脆爽的为佳，如莲藕，竹笋、蒜薹等，也可按个人喜好加入其它蔬菜。"},
          {id:"4",image:"./static/image/recommendFood4.jpg",name:"水磨丝",grade:"",info:"“水磨丝”以猪耳为原料，因将猪耳传统名菜平面片开，可见水磨石般花，故名。是陕西大荔县传统名菜，距今已有200\n" +
              "\n" +
              "　　余年历史。此菜用料普通，但以刀功精湛而名噪遇选。制作此菜，须将猪耳片薄、切细，成菜细如毛发，蓬松透明，脆嫩光滑，香酸爽口，风味独特，每令食客称奇，行家叫绝。"},
          {id:"5",image:"./static/image/recommendFood5.jpg",name:"月牙烧饼",grade:"",info:"大荔月牙烧饼是陕西省渭南市大荔县的特产。大荔月牙烧饼因状如月牙而得名。特点是外皮焦脆，内瓤酥松空软，热吃凉吃均可，吃水盆羊肉时，用此饼夹入羊肉，饶有风味。\n" +
              "　　关中有句俗话，\"大荔有一怪，烧饼半个卖。\"这半个卖的烧饼，颜色金黄，状如新月，空心饱满，酥香可口，所以，人称月牙烧饼。\n" +
              "\n" +
              "　　大荔月牙烧饼大荔月牙烧饼好看也好吃。表皮黄亮，有些脆。内瓢松软，有点甜。虽说只一指头多厚薄，却又有若干层层，油油的。虽说那里边是空的，外边却沾着不少的芝麻，香香的。有的说：“走南京，到北京，同州府（今大荔县）里好烧饼。热吃脆，冷吃酥，芝麻粒粒油层层”。有的在“热吃脆，冷吃酥”之后，加了个“沙温吃了不咳嗽”。“沙温”，关中方言，形容那温度不热不冷，正好合适。还有的说：“南京到北京，同州城里好烧饼，拈起轻，摸着厚，里面空空能夹肉”。当然，不只能夹肉，也能夹各种蔬菜，夹鸡蛋，肉夹馍，菜夹馍，蛋夹馍。\n" +
              "\n" +
              "　　大荔人吃月牙烧饼大荔人吃月牙烧饼，一般是跟清汤羊肉一起吃的。也就是要夹了羊肉来吃。半个烧饼，又是有着层层的半个烧饼，中间是空的，倒是非常适宜于夹了肉吃。所以，也有人说，大荔人聪明，发明这样的月牙形状的烧饼，就是为了吃这样的清汤羊肉。《大荔县志》肯定了这种可能性，说：“月牙烧饼，是由于人们食用水盆羊肉的需要而渐次形成的。过去吃羊肉时，以烧饼为主食。早先的烧饼是圆形，要夹肉，还需把烧饼掰成两半，既参差不齐，又不方便。在生活实践中，人们逐渐意识到，与其把烧饼掰成两半吃，还不如制作时，将烧饼切成两半，既方便也美观，烧饼就由圆形改为半圆形。因其状如月牙，故名‘月牙烧饼’。”这可能是一种比较切合实际的月牙烧饼产生原因的说法，也是对大荔勤劳善良的百姓的一种赞美。"},
          {id:"6",image:"./static/image/recommendFood6.jpg",name:"羌白浆水面",grade:"",
            info:"在陕西关中大荔县农村，每到“烈日炎炎似火烧”的盛夏，几乎家家户户都制浆水、吃浆水面，而最为拿手的是大荔县羌白镇这家浆水面，做的浆水是女店主从娘家带来已有流传百年的浆水橛子【即根子或称“酵子”】做的浆水，离远都能闻到鲜美的味道，酸而不烈，气味芬芳堪称一绝。做浆水面时，先将沤制好的浆水舀一部分入锅，并根据其酸味的浓度和食者的要求，酌情加入清水兑合，烧之使沸，再加入葱花、香菜、食盐、辣椒等调味品，待凉即成。煮熟的面条，调人清凉的浆水，看上去嫩黄柳绿，红油浮面，酸辣可口，色味俱佳吃一碗顿觉清凉透心，别有风味。做浆水面，面条的种类不拘，擀压拉均立（即）可，其关键在于浆冰（水）沤制的好坏。据店主介绍，一般农村人做浆水不少都用的是上辈人存留的浆水“根子”，冬季密封、夏季打开，年年晾嗮“续浆水”，俗称\"投浆水\"倒入新鲜面汤入缸内，在太阳下暴晒发酵，日晒六七天，汤呈乳白色，即有浓酸味，并带有清淡的特殊香气，这时便可取用，即可成为上好的浆水了。天气热时，对浆水菜要精心侍弄，在食用的过程中，不断地倒缸清底，去除沉淀物，并不断地加以凉面汤。总之要常吃常新，不宜久储不用。盛浆水的器皿不能用铁的，（必须）最好以陶器为好，这样做的浆水味道才能正宗，清淡味醇，余味悠长。"},
          {id:"7",image:"./static/image/recommendFood10.jpg",name:"枣肉沫糊",grade:"",info:"　大荔枣肉沫糊,源于唐代,是以当地\"三大宝\"之一的'马牙枣'为主料制作的,另加面粉、豇豆、碱面、糖等精制枣肉汁、制豇豆汤、制沫糊三道工序熬制而成,其色红味甜,滑润可口,健脾开胃,老幼皆宜."},
          {id:"8",image:"./static/image/recommendFood8.jpg",name:"带把肘子",grade:"",info:"　大荔带把肘子源远流长。 是陕西大荔传统名菜，为明朝弘治年间同州（今大荔县）城里厨师李玉山创制。陕西大荔带把肘子，属蒸菜类，在秦馔筵席上久负盛名，《中国菜谱》秦菜部分把其列为第一名菜。作酒菜、饭菜均可。色泽枣红，如把柄，故称“带把肘子”。\n" +
              "　　它的用料别致，不同于其它肘菜。肘子带骨带蹄，成菜如丘，造形别致、丰满，堪称“盘中一王”。\n" +
              "\n" +
              "　　基本制作工艺是：将带脚爪的猪前肘刮洗干净，肘头朝外，肘把（肘爪）朝里，肘皮朝下放在砧板上，用刀在正中由肘头向肘把沿着腿骨将皮刨开，剔去脚骨两边的肉（三面离肉），底部骨与肉相连，使骨头露出，然后将两节腿骨由中间用刀背砸断，入汤锅煮至七成熟捞出，辗干，趁热用红酱油涂抹肉皮。取蒸盆，底放八角、桂皮等调料，再把骸骨用手掰断，不伤外皮，将肘皮朝下装入蒸盆，整成图形，撒入精盐，用消过毒的净纱布盖在肉上，再将甜面酱、葱、红豆腐乳、红着油、白酱油、姜、蒜等在纱布上抹开，上笼蒸熟蒸烂取出，揭去纱布，扣入盘中，拣去八角，上桌时另带葱段和甜面酱小碟。\n" +
              "\n" +
              "　　其风味特点是：肘肉酥烂不腻，肘皮胶粘，香醇味美。是一道富含蛋白质、钙质的菜肴。"}
        ]
      }
    }
  }
</script>

