<template>
  <div class="list">
    <van-row v-for="(item,index) in dataTransfer" :key="index">
      <van-col span="12" style="border:1px solid transparent" >
        <img :src="item.image" alt="">
      </van-col>
      <van-col span="12" style="border:1px solid #cccccc;border-left: none;padding: 30px">
        <h3 class="title">{{item.name}}</h3>
        <p class="fruitInfo">{{item.info}}</p>
      </van-col>
    </van-row>
  </div>
</template>

<script>
  import {Row,Col} from 'vant'
    export default {
        name: "HometownFruits",
      components:{[Row.name]:Row,[Col.name]:Col},
      props:{
        dataTransfer:Array,
      }
    }
</script>

<style scoped>
.list{ width: 100%;display: flex;justify-content: center;flex-wrap: wrap}
.list .van-row{ height: 300px;width: 80%;margin-bottom: 30px}
.list .van-row:hover{ box-shadow: 0 5px 10px #000;cursor: pointer}
.list .van-row .van-col{ height: 100%;overflow: hidden}
.list .van-row .van-col img{ width: 100%;height: 100%}
.list .van-row .van-col .title{ line-height: 60px;border-bottom: 1px solid #cccccc;text-align: left;font-size: 20px}
.list .van-row .van-col .fruitInfo{ font-size: 14px;line-height: 28px;text-align: left;margin-top: 20px;text-indent: 20px}
</style>



