<template>
    <div class="recommend">
      <van-row gutter="20" type="flex">
        <van-col v-for="(item,index) in dataTransfer" :key="index">
          <div class="recommendBorder">
            <img :src="item.image" alt="">
            <p>{{item.name}}</p>
            <p class="grade">{{item.grade}}</p>
            <p class="info">{{item.info}}</p>
          </div>
        </van-col>
      </van-row>
    </div>
</template>

<script>
  import { Row,Col } from 'vant'
    export default {
      name: "HometownRecommend",
      components:{
        [Row.name]:Row,
        [Col.name]:Col
      },
      props:{
        dataTransfer:Array,
      }
    }
</script>

<style scoped>
.recommend .van-row{ flex-wrap: wrap;justify-content: space-between}
.recommend .van-col{ width: 290px;margin-bottom: 30px}
.recommend .van-col:hover{box-shadow: 0 5px 10px #000;cursor: pointer}
.recommendBorder{ border:2px solid #ebebeb;padding:5px;border-radius: 10px;height: 400px;overflow:hidden}
.recommendBorder img{ width: 100%;height:200px;border-radius: 10px}
.recommendBorder p{ line-height: 50px}
.recommendBorder .grade{ line-height: 30px;color:gray;height: auto;white-space: nowrap;overflow: hidden}
.recommendBorder .info{ line-height: 30px;font-size: 12px;text-indent: 20px;overflow: hidden;text-overflow: ellipsis;display: -webkit-box;line-clamp: 4;box-orient: vertical;}
</style>



