<template>
    <div id="header">
      <router-link to="/">
        <img src="../../static/image/logo.png" alt="">
      </router-link>
      <el-menu
        :default-active="this.$router.path"
        router
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        background-color="#333945"
        text-color="#fff"
        active-text-color="#00b896"
      >
        <el-menu-item v-for="(item,i) in headerList" :key="i" :index="item.name">{{item.title}}</el-menu-item>
      </el-menu>
    </div>
</template>

<script>
export default {
  name: "HometownHeader",
  data(){
    return {
      headerList:[
        {id:1,title:"网站首页",name:"/"},
        {id:2,title:"舌尖美食",name:"/food"},
        {id:3,title:"玩转景区",name:"/play"},
        {id:4,title:"瓜果梨枣",name:"/fruit"}
      ]
    }
  },
  methods:{
    handleSelect(key, keyPath) {
      // console.log(key, keyPath);
    },
    handleScroll(){
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      var offsetTop = document.querySelector(".el-menu-demo").offsetTop
      if(scrollTop>=300){
        let headerDom = document.querySelector("#header")
        headerDom.style.position = "fixed"
        headerDom.style.zIndex = "5"
      }
    }
  },
  mounted(){
    window.addEventListener('scroll',this.handleScroll)
  }
}
</script>

<style scoped>
#header{width: 100%;height: 100px;display: flex;justify-content: space-around;align-items: center;border-bottom:3px solid #00b896;background-color: #333945}
#header img{ border-radius: 50%}
#header .el-menu{ width: 36%}
#header .el-menu--horizontal{ border-bottom:3px solid #00b896;display: flex;justify-content: space-between}
#header .el-menu--horizontal>.el-menu-item{ height: 100%;line-height: 100px}
</style>




