<template>
    <div>
      <div class="list">
        <van-row gutter="30" v-for="(item,index) in dataTransfer" :key="index" type="flex" justify="space-between" @click="goDdetial(item.id)">
            <van-col span="6">
                <img :src="item.image" alt="">
            </van-col>
            <van-col span="6" style="padding-right: 40px">
              <h3>{{item.name}}</h3>
              <p class="foodInfo">{{item.info}}</p>
              <p v-show="price" class="price">{{item.price}}</p>
              <p v-show="address" class="address">地址:{{item.address}}</p>
            </van-col>
        </van-row>
      </div>
    </div>
</template>

<script>
  import {Row,Col} from 'vant'
  export default {
    name: "HometownProduct",
    components: {
      [Row.name]:Row,
      [Col.name]:Col
    },
    props:{
      dataTransfer:Array,
    },
    data(){
      return {
        price:true,
        address:true
      }
    },
   created(){
      if(this.$route.path == "/food"){
        this.price = false
        this.address = false
      }
   }


  }
</script>

<style scoped>
.list{width: 100%; overflow-x: hidden;display: flex;flex-wrap: wrap;justify-content: center}
.list .van-row{width: 50%;margin-bottom: 50px}
.list .van-row:hover{ cursor: pointer;}
.list .van-row .van-col{ width: 100%;text-align: left;overflow: hidden}
.list .van-row img{width: 300px;height: 200px;border-radius: 5px}
.list .van-row img:hover{position:absolute;width: 302px;height:202px;opacity:.6;transition: all 2s;transform: scale(1.1)}
.list .van-row .van-col h3{ line-height: 50px}
.list .van-row .foodInfo{ font-size: 12px;line-height: 24px;text-align: left;text-indent: 20px}
.list .van-row .van-col:nth-of-type(2){ position: relative}
.list .van-row .van-col:nth-of-type(2) .el-button{ position: absolute;bottom:0;z-index: 5}
.list .price,.list .address{ font-size: 12px;line-height: 40px}
</style>



