<template>
    <div>
      <hometown-header></hometown-header>
      <div class="banner">
        <img src="../../../static/image/recommendFood4.jpg" alt="">
      </div>
      <div class="Title">
        <span>特色美食</span>
      </div>

      <hometown-product :dataTransfer="foodList"></hometown-product>

      <hometown-footer></hometown-footer>
    </div>
</template>

<script>

  import HometownHeader from "../../components/HometownHeader";
  import HometownProduct from "../../components/HometownProduct";
  import HometownFooter from "../../components/HometownFooter";
  export default {
      name: "FoodIndex",
      components: {
        HometownFooter,
        HometownProduct,
        HometownHeader
      },
    data(){
      return {
        foodList:[
          {id:"1",image:"./static/image/food1.jpg",name:"大荔羊肉泡",info:"羊肉泡馍是陕西当地的著名小吃。古时候被称为\"羊羹\"，是一种难得的滋补佳品，因它暖胃耐饥，素为西安和西北地区各族人民所喜爱，外宾来陕也争先品尝，以饱口福。"},
          {id:"2",image:"./static/image/food2.jpg",name:"胖嫂夹心饼",info:"根据不同消费者的口味，用五香 香辣 豆沙 白糖四种馅料，进行加工，吃起来软酥可口、皮薄馅多、味香扑鼻、营养又健康，既可当作主食，又可当作茶余饭后的零食。"},
          {id:"3",image:"./static/image/food3.jpg",name:"蜜汁轱辘",info:"在陕西大荔，有种可以吃的“轱辘”，是咱大陕西的名菜，因为含糖（喝酒时，糖有护肝作用），是一道非常好的下酒菜。"},
          {id:"4",image:"./static/image/food4.jpg",name:"水磨丝",info:"“水磨丝”以猪耳为原料，因将猪耳传统名菜平面片开，可见水磨石般花，故名。是陕西大荔县传统名菜。此菜用料普通，但以刀功精湛而名噪遇选。"},
          {id:"5",image:"./static/image/food5.jpg",name:"带把肘子",info:"带把肘子是陕西大荔极具地方特色的一道传统名菜。作酒菜、饭菜均可。以香味突出，色、香、味、形俱佳著称。它的用料别致，不同于其它肘菜。肘子带骨带蹄，成菜如丘，造形别致、丰满，堪称“盘中一王。"},
          {id:"6",image:"./static/image/food6.jpg",name:"泡油糕",info:"泡油糕是中国特色糕点，由唐代佳点‘见见消’（油浴饼）演变而来。糕面隆起，泡泡蓬松，其味芬芳，入口即消。形状玲珑剔透，犹如巧制凌花。"},
          {id:"7",image:"./static/image/food7.jpg",name:"蜜枣晋糕",info:"将江米、蚕豆和红枣在一起蒸的软软的，吃的时候还可以放点白糖，吃在嘴里软软的甜甜的。"},
          {id:"8",image:"./static/image/food8.jpg",name:"枣肉沫糊",info:"　大荔枣肉沫糊,源于唐代,是以当地\"三大宝\"之一的'马牙枣'为主料制作的,另加面粉、豇豆、碱面、糖等精制枣肉汁、制豇豆汤、制沫糊三道工序熬制而成,其色红味甜,滑润可口,健脾开胃,老幼皆宜"},
          {id:"9",image:"./static/image/food9.jpg",name:"时辰包子",info:"他家的包子特别味长，每天争尝名品的食客络绎不绝，排成长龙，一过上午时，就买不到包子了。因此人们就给这种包子加上“时辰”两字。其特点为面细皮白“僧帽”状，油渗包底呈金黄，馥郁鲜爽，油而不腻，香味悠长。"},
          {id:"10",image:"./static/image/food10.jpg",name:"八宝辣子",info:"八宝辣子口感脆爽，香辣开胃，用于佐餐夹馍，堪称“饭扫光”、“馍遭殃”。炒制八宝辣子是一个看起来简单的活，但要做好、做地道还真有许多讲究。"},
        ]
      }
    }
  }
</script>

<style scoped>
.Title{ height: 150px}
.Title span{border-bottom:3px solid #00b896;padding-bottom: 20px;line-height: 100px;font-size: 30px}
</style>



