            {id:"1",image:"./static/image/fruit1.jpg",name:"金太阳杏",info:"大荔属暖温半干旱大陆性季风气候区。四季分明，昼夜温差大，保障杏子在充足的日照下可口多汁，一年收获一次，只为你品尝大自然的清香。新鲜甜杏子，天然水果孕妇必备。" +
