            {id:"2",image:"./static/image/fruit2.jpg",name:"油蟠桃 ",info:"蟠桃是较珍贵水果之一，是桃子的一种，形状扁圆，顶部凹陷形成一个小窝，其果皮呈深黄色，顶部有一片红晕，味甜汁多，不愧为仙桃之称。" +
