            {id:"3",image:"./static/image/fruit3.jpg",name:"水蜜桃",info:"水蜜桃属于一种球形可食用水果类，水蜜桃有美肤、清胃、润肺、祛痰等功能。它的蛋白质的含量比苹果、葡萄高一倍，比梨子多七倍；铁的含量比苹果多三倍，比梨子多五倍，素有“果中皇后”的美誉。我国多有栽种。富含多种维生素，其中维生素C最高 。水蜜桃树属于落叶小乔木植物，一般树高3-4米。干性弱，树冠开张。幼树生长旺盛，1年生长枝达1米以上。"},
            {id:"4",image:"./static/image/fruit4.jpg",name:"白蜜一号油桃",info:"富含维生素C，油桃由桃子改良栽培而成，比桃子甜，营养也丰富一些。一个新鲜油桃所含的维生素C，几乎可以满足成人一天所需。维生素C不仅有助于身体吸收铁和维护免疫系统，而且对合成皮肤的重要组成部分胶原至关重要。胶原还能促进伤口结疤，对伤口愈合十分重要。 油桃的皮比桃子光滑，没有讨厌的绒毛。油桃所含热量比桃子略多，平均为三十六大卡。"},
            {id:"5",image:"./static/image/fruit5.jpg",name:"冬枣",info:"大荔冬枣具有四大特征，果个大，果实近圆形，平均单果重20克，果面平整光洁；果皮薄而酥脆，完熟期呈浅黄片状赭红色；肉细嫩，果肉乳白色，口感细嫩酥脆；味香甜。冬枣含人体所需的19氨基酸和维生素A、维生素B、维生素C、维生素P，含可溶性固形物34-38%，维生素C352毫克/100克，营养价值为“百果之冠”，被誉为“百果王”、“活维生素丸”。"},
            {id:"6",image:"./static/image/fruit6.jpg",name:"蜜瓜" +
                "",info:"含有大量的水分和维生素，具有生津止渴，除烦热，宁心神等作用。\n" +
