            {id:"7",image:"./static/image/fruit7.jpg",name:"酥梨",info:"酥梨为一种喜闻乐见的水果品种，以其表面光洁，个大丰满，多汁酥脆，营养丰富和无公害等几大特点而深受国内外消费者喜爱，在我国有多个品种，畅销海内外。酥梨含有糖份，矿物质，有机酸和多种维生素，营养极为丰富，是不可多得的果中佳品，除鲜食外，还可加工成梨酒、梨膏梨糖和罐头等，是食品工业的重要原料。梨汁、梨膏具有去热清痰止咳润肺等药用价值，是人们日常十分喜爱的辅助药剂。"},
            {id:"8",image:"./static/image/fruit8.jpg",name:"大棚西瓜",info:"西瓜堪称“盛夏之王”，清爽解渴，味道甘味多汁，是盛夏佳果，西瓜除不含脂肪和胆固醇外，含有大量葡萄糖、苹果酸、果糖、蛋白氨基酸、番茄素及丰富的维生素C等物质，是一种富有很高的营养、纯净、食用安全食品。瓤肉含糖量一般为5-12%，包括葡萄糖、果糖和蔗糖。甜度随成熟后期蔗糖的增加而增加。孕妇在妊娠期间常吃些西瓜，不但可以补充体内的营养消耗，同时还会使胎儿的营养摄取得到更好的满足。"},
            {id:"9",image:"./static/image/fruit9.jpg",name:"嘎啦苹果",info:"嘎啦苹果中含有神奇的“嘎啦苹果酚”，极易在水中溶解，易被\n" +
