            {id:"11",image:"./static/image/fruit11.jpg",name:"红啤梨",info:"红啤梨果面青红，果体硬度好，肉细嫩、口感甜、果汁多，清甜的味道非常适合大众口味。吃的时候可以提前在常温放1天到2天，酥软了再吃，这样才能真正享受啤梨的好味道。红啤梨含有丰富的胡萝卜素、维生素B1、B2及苹果酸等，具有“润肺凉心,消炎降火,解疮毒、酒毒”的作用。红啤梨含大量果糖，是属于高热量的食物。红啤梨很少会引起肠胃不适，几乎人人都可以吃，甚至婴儿也可以食用。红啤梨还含有钾，有助调节血压。"},
            {id:"12",image:"./static/image/fruit12.jpg",name:"樱桃",info:"樱桃可以缓解贫血。铁是合成人体血红蛋白的原料，对于女性来说，有着极为重要的意义。世界卫生组织的调查表明，大约有50%的女童、20%的成年女性、40%的孕妇会发生缺铁性贫血。另外，樱桃虽好，但也注意不要多吃。因为其中除了含铁多以外，还含有一定量的氰甙，若食用过多会引起铁中毒或氰化物中毒。"},
            {id:"13",image:"./static/image/fruit13.jpg",name:"高石脆瓜",info:"高石脆瓜生产区属暖温带半湿润、半干旱季风气候通风好，光照足，昼夜温差大自然条件非常有利于高石脆瓜种植。高石脆瓜具有五大特色，可用五个字概括之：香、脆、酥、甜、美。"},
          ]
        }
      }
    }
</script>

