<template>
  <div>
    <div v-for="(item,index) in foodList" :key="index">
      <img :src="item.image" alt="">
      <el-tag type="info" v-for="(t,i) in item.tag" :key="i">{{t.tag}}</el-tag>
      <h3 class="title">{{item.title}}</h3>
      <p class="foodInfo">{{item.info}}</p>
      <p class="tips">
        <span>小贴士</span>
        {{item.tips}}
      </p>
    </div>
  </div>

</template>

<script>
    export default {
        name: "FoodDetial",
      data(){
          return {
            foodList: [
              {id:'1',image:"../../../static/image/food1.jpg",
                tag:[
                  {tag:"西北菜"},
                  {tag:"富含蛋白质"},
                  {tag:"补体虚"},
                  {tag:"补温气血"},
                  {tag:"驱寒冷"}
                ],
                title:"羊肉泡家常做法",
                info:"" +
                  "1.羊肉放清水中浸泡一晚，将羊肉上的羊油取下来切小块备用+\n" +
                  "2.泡好的羊肉放入砂锅中+\n" +
                  "3.放入小茴香和花椒大火烧开+\n" +
                  "4.撇去表层浮沫，然后小火炖煮+\n" +
                  "5.炒锅中放入几滴食用油，然后将取下来的羊油倒入开始小火炸羊油+\n" +
                  "6.炸至羊油块中的油出尽+\n" +
                  "7.然后将热的羊油浇入干辣椒面中，制成羊油辣子备用+\n" +
                  "8.葱切葱花+\n" +
                  "9.炖煮大约三小时，羊肉熟+\n" +
                  "10.将煮熟的羊肉捞出切块，放入碗中，然后放入葱花+\n" +
                  "11.将热的羊肉汤浇入碗中，不吃辣椒的人放入少量盐就可以吃了+\n" +
                  "12.喜欢吃辣椒的，将烧好的羊油辣子调入即可+\n" +
                  "8.葱切葱花+\n" +
                  "",
                tips:"1、羊肉放在清水中浸泡，是为了拔尽肉中血渍。\n" +
                  "2、将有肉上的羊油取下，一是为了煮出来的羊肉汤不油腻，二是为了制作正宗的羊油油泼辣子。\n" +
                  "3、羊肉汤炖煮本是要放入花椒、桂皮、大茴、小茴、草果等大料，我是因为怕上火，所以只放了最主要的两味。"

              }
            ]
          }
      }
    }
</script>

<style scoped>

</style>



