import Vue from 'vue'
import Router from 'vue-router'
import HometownIndex from '../views/index/HometownIndex'
import FoodIndex from '../views/index/FoodIndex'
import PlayIndex from '../views/index/PlayIndex'
import FruitsIndex from '../views/index/FruitsIndex'
import FoodDetail from '../views/detial/FoodDetial'


Vue.use(Router)

export default new Router({
  routes: [
    // index
    {
      path: '/',
      name: 'HometownIndex',
      component: HometownIndex
    },
    {
      path: '/food',
      name: 'FoodIndex',
      component: FoodIndex
    },
    {
      path: '/play',
      name: 'PlayIndex',
      component: PlayIndex
    },
    {
      path: '/fruit',
      name: 'FruitsIndex',
      component: FruitsIndex
    },

    // 美食详情
    {
      path: '/fooddetail',
      name: 'FoodDetail',
      component: FoodDetail
    }
  ]
})



